.layout {
  overflow: hidden;
  display: grid;
  grid-template-columns: 5fr 3fr;
  position: relative;
}

.layoutflow {
  flex-grow: 1;
  position: relative;
  height: 80vh;
  background-color: rgb(255, 255, 255);
}


.react-flow__node[data-type="parentNode"] {
  margin: 0px;
}

.controls {
  position: absolute;
  right: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  z-index: 100;
  width: auto;
  margin-top: 1rem;
}

.controls>button,
.controls>div>button {
  color: #443E99;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  width: 185px;
  height: 51px;
  background: rgb(241, 240, 255);
  border: none;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.controls>div>button :focus {
  outline: none;
}

.controls>div>button>img {
  margin-right: 70.08px;
}

.detailsPositioning {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.detailsBox {
  background: #fbfbfc;
  width: 100%;
  min-height: 80%;
  border-radius: 6px;
  padding: 3rem;
  font-family: Rubik;
  border: 1px var(--lightGrey) solid;
  position: relative;
  margin-bottom: 2rem;
}

.details {
  font-size: 16px;
  display: grid;
}

.detailsTitle {
  font-weight: 600;
  margin-bottom: 2rem;
  font-size: 2.6rem;
  color: var(--black);
  line-height: 4.8rem;
}

.detailsLabel {
  font-family: RubikBold;
  margin-bottom: 1rem;
  font-size: 1.6rem;
}

.detailsP {
  margin-bottom: 2rem;
  border-radius: 6px;
  color: var(--black);
  font-size: 1.8rem;
}

.detailsNoNode {
  font-size: 16px;
  display: grid;
  place-items: center;
  color: var(--black);
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.URI {
  word-wrap: break-word;
  max-width: 35ch;
}


.detailsKey {
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.detailsKey:hover {
  white-space: normal;
  overflow: visible;
}

.detailsValue {
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-indent: 20px;
  color: #A1A4AB;
}

.detailsValue:hover {
  white-space: normal;
  overflow: visible;
}

.legend {
  /* Ali edit id#123456789 */
