@media (max-width: 1850px) {
  .URI {
    max-width: 30ch;
  }
}

@media (max-width: 1650px) {
  .URI {
    max-width: 25ch;
  }
}

@media (max-width: 1350px) {
  .layout {
    display: block;
  }

  .layoutflow {
    height: 500px;
  }

  .detailsPositioning {
    align-items: center;
    margin-bottom: 4rem;
  }

  .detailsBox {
    padding: 1.5rem;
    margin-bottom: 0rem;
  }

  .detailsTitle {
    margin-bottom: 1.5rem;
  }

  .detailsNoNode {
    display: block;
    position: relative;
  }

  .URI {
    max-width: 100%;
  }
}

@media (max-width: 550px) {
  .URI {
    max-width: 32ch;
  }

  .legend {
    grid-template-columns: 1fr;
    width: max-content;
  }
}

@media (max-width: 400px) {
  .URI {
    max-width: 22ch;
  }
}

@media (max-width: 275px) {
  .URI {
    max-width: 10ch;
  }
}

